@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700&display=swap);
* {
  font-family: 'Varela Round', sans-serif;
  font-weight: normal;
  color: #080522;
  color: var(--black); }

body {
  margin: 0; }

:root {
  --primary:#F4F45C;
  --black:#080522;
  --dark:#3A2C3E;
  --light:#EFEFEF;
  --white:#ffffff; }

.App {
  height: 100vh;
  width: 100%;
  background-color: #080522;
  background-color: var(--black);
  position: relative;
  overflow: hidden; }

.section {
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative; }

.container {
  width: 1070px;
  margin: 100px auto;
  position: relative;
  z-index: 2; }
  .container .row {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0 -25px; }
  .container .col {
    width: 100%;
    margin: 0 25px 25px; }
    .container .col-2 {
      width: 50%; }

h1 {
  font-size: 42px;
  font-weight: bold;
  margin-top: 0; }

.btn {
  padding: 15px 30px;
  border-radius: 50px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.1rem;
  display: inline; }
  .btn--primary {
    color: var(--white);
    background-color: var(--primary); }

.next-page {
  position: absolute;
  bottom: 10px;
  left: calc(50% - 25px);
  height: 50px;
  width: 50px;
  background-color: var(--white);
  border-radius: 50px;
  transition: bottom .3s ease-in-out; }
  .next-page.open {
    bottom: -25px; }

.nav {
  position: relative;
  background-color: transparent;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 60px;
  -webkit-justify-content: center;
          justify-content: center;
  height: 10vh;
  transition: all .6s ease-in-out; }
  .nav.open {
    height: 100vh; }
    .nav.open .brand {
      margin-top: 0; }
    .nav.open .brand.aside {
      left: -50%; }
    .nav.open .socials.aside {
      right: -500px; }
  .nav .brand {
    height: auto;
    margin-top: -375px;
    transition: all .6s ease-in-out;
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center; }
    .nav .brand.aside {
      position: absolute;
      margin-top: 0;
      font-size: 24px;
      left: 50px; }
      .nav .brand.aside .logo {
        display: none; }
      .nav .brand.aside .name {
        font-size: 24px; }
    .nav .brand .logo {
      height: 300px;
      width: 300px;
      border-radius: 100%;
      margin-bottom: 45px; }
    .nav .brand .name {
      color: var(--white);
      font-size: 42px;
      font-weight: bold; }
  .nav .socials {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    transition: all .6s ease-in-out; }
    .nav .socials.aside {
      position: absolute;
      margin-top: 0;
      font-size: 24px;
      right: 50px; }
    .nav .socials .social {
      width: 40px;
      height: 40px;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      background-color: rgba(255, 255, 255, 0.1);
      border-radius: 50%;
      margin: 0 10px;
      transition: all .6s ease-in-out; }
      .nav .socials .social--icon {
        max-height: 20px;
        width: 25px; }
      .nav .socials .social:hover {
        background-color: rgba(255, 255, 255, 0.1);
        cursor: pointer; }
        .nav .socials .social:hover .social--icon {
          opacity: 1;
          font-weight: bold; }
  .nav .menu {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 60px 0; }
    .nav .menu .menu-item {
      display: -webkit-flex;
      display: flex;
      height: 50px;
      padding: 0 25px;
      -webkit-align-items: center;
              align-items: center;
      background-color: rgba(255, 255, 255, 0.1);
      border-radius: 10px;
      margin: 0 25px; }
      .nav .menu .menu-item--title {
        color: var(--white);
        opacity: .5;
        font-weight: bold; }
      .nav .menu .menu-item.active {
        background-color: rgba(255, 255, 255, 0.1); }
        .nav .menu .menu-item.active .menu-item--title {
          opacity: 1; }
      .nav .menu .menu-item:hover {
        cursor: pointer; }
        .nav .menu .menu-item:hover .menu-item--title {
          opacity: 1;
          font-weight: bold; }

.section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
  height: 400px;
  background-color: rgba(0, 0, 0, 0.1); }
  .section--light {
    background-color: var(--light); }
  .section--white {
    background-color: var(--white); }

.body {
  position: relative;
  overflow: hidden;
  height: calc(90vh - 30px);
  width: 1200px;
  background-color: var(--white);
  margin: 0 auto;
  border-radius: 10px;
  overflow-y: auto; }

.image {
  height: 300px;
  width: 300px; }

.left {
  float: left;
  width: 50%;
  text-align: center;
  margin: auto 0; }

.right {
  float: right;
  width: 50%;
  text-align: center;
  margin: auto 0; }

